import React, { useEffect, useRef, useState } from "react";
import "./styles.css";
import QRCodeStyling from "qr-code-styling";
import { toPng, toJpeg, toBlob, toPixelData, toSvg } from "html-to-image";

const qrCode = new QRCodeStyling({
  width: 330,
  height: 330,
  backgroundOptions: {
    color: "#1e1e1e"
  },
  margin: 10,
  image: "/vizion_qr.svg",
  dotsOptions: {
    color: "#fd0",
    type: "dots"
  },
  cornersSquareOptions: {
    type: "circle",
    color: "#fd0"
  },
  imageOptions: {
    imageSize: 0.5,
    crossOrigin: "anonymous"
  }
});

export default function App() {
  const [name, setName] = useState("Name Here");
  const [title, setTitle] = useState("Title Here");
  const [url, setUrl] = useState("https://www.vizionapi.com");
  const [fileExt, setFileExt] = useState("svg");
  const ref = useRef(null);
  const wrapperRef = useRef(null);

  useEffect(() => {
    qrCode.append(ref.current);
  }, []);

  useEffect(() => {
    qrCode.update({
      data: url
    });
  }, [url]);

  const onUrlChange = (event) => {
    event.preventDefault();
    setUrl(event.target.value);
  };

  const onExtensionChange = (event) => {
    setFileExt(event.target.value);
  };

  const onDownloadClick = async () => {
    // qrCode.download({
    //   extension: fileExt
    // });
    let conversionFn = toSvg;
    switch (fileExt) {
      case "jpeg":
        conversionFn = toJpeg;
        break;
      case "png":
        conversionFn = toPng;
        break;
      case "svg":
        conversionFn = toSvg;
        break;
      default:
        break;
    }
    const dataUrl = await conversionFn(wrapperRef.current, {
      width: 370,
      canvasWidth: 370,
      backgroundColor: "#1e1e1e"
    });
    const link = document.createElement("a");
    link.download = `${name}.${fileExt}`;
    link.href = dataUrl;
    link.click();
  };

  const onNameChange = (e) => {
    e.preventDefault();
    setName(e.target.value);
  };

  const onTitleChange = (e) => {
    e.preventDefault();
    setTitle(e.target.value);
  };

  return (
    <div className="App">
      <input value={name} onChange={onNameChange} />
      <input value={title} onChange={onTitleChange} />
      <div style={styles.inputWrapper}>
        <input value={url} onChange={onUrlChange} style={styles.inputBox} />
        <select onChange={onExtensionChange} value={fileExt}>
          <option value="png">PNG</option>
          <option value="svg">SVG</option>
          <option value="jpeg">JPEG</option>
        </select>
        <button onClick={onDownloadClick}>Download</button>
      </div>
      <div style={styles.qrWrapper} ref={wrapperRef}>
        <h1 style={styles.name}>{name}</h1>
        <h3 style={styles.title}>{title}</h3>
        <div ref={ref} />
      </div>
    </div>
  );
}

const styles = {
  inputWrapper: {
    margin: "20px 0",
    display: "flex",
    justifyContent: "space-between",
    width: "100%"
  },
  inputBox: {
    flexGrow: 1,
    marginRight: 20
  },
  name: {
    color: "#dedede",
    marginBottom: ".5rem",
    paddingTop: 20,
    paddingBottom: 0
  },
  title: {
    marginTop: 0,
    color: "#dedede",
    marginBottom: "3rem"
  },
  qrWrapper: {
    width: 370,
    height: 600,
    backgroundColor: "#1e1e1e",
    paddingBottom: "4rem"
  }
};
